@import "../../vars";

.nav {
  z-index: 10;
  position: relative;
}

:global {
  nav {
    position: fixed;
    bottom: 0;
    top: 0;
    right: 0;
    margin: auto;
    transform: translateX($nav-width);
    transition: all .5s ease-out;
    z-index: 20;
    background: lighten(#222, 10%);
    height: 100%;
    width: $nav-width;

    &.active {
      transform: translateX(0);
    }
  }
}

.navigationButton {
  position: fixed;
  right: 10px;
  top: 10px;
  background: #fff;
  color: #000;
  border: none;
  padding: 12px 10px;
  cursor: pointer;
  border-radius: 50%;
  z-index: 30;
  width: 45px;
  box-sizing: border-box;

  $burger-bar: 3px;

  span {
    display: block;
    height: $burger-bar;
    width: 100%;
    background: #222;
    margin-bottom: $burger-bar + 1;
    transition: all .3s ease-out;
    position: relative;

    &:nth-of-type(2) {

    }

    &:nth-of-type(3) {
      margin-bottom: 0;
    }
  }

  &.active {
      span {
        transform: rotate(45deg);
        top: 7px;

        &:nth-of-type(2) {
          top: 0
        }
    
        &:nth-of-type(3) {
          transform: rotate(-45deg);
          top: -6px;
        }
      }
  }
}

.navItems {
  list-style: none;
  margin: 0;
  padding: 0;

  li {
    display: block;
    margin: 10px;
    a {
      color: #fff;
      text-transform: uppercase;
      font-weight: bold;
      text-decoration: none;
      padding: 8px 10px;
      display: block;
    }
  }
}
