.arrow {
  font-size: 50vh;
  background: #222;
  color: #fff;
  padding: 30px 0;
  position: relative;


  .icon {
    transform: rotate(0deg);
    transition: all .5s ease-out;
    display: block;

    &.disabled {
      opacity: .5;
    }

    &.up {
      transform: rotate(360deg);
    }
    &.down {
      transform: rotate(180deg);
    }
  }
}

.text {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  font-weight: bold;
  font-size: 18px;
  color: #fff;
  display: flex;
  justify-content: center;
}