.App {
  text-align: center;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: 0;
  right: 0;
  margin: auto;
}

.button {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  margin: auto;
  z-index: 3;
  height: 50%;
  width: 100%;
  cursor: pointer;

  &.bottom {
    top: auto;
    bottom: 0;
  }
}

.reset {
  position: absolute;
  z-index: 1;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
  outline: 0;
  cursor: pointer;
}