.App {
  text-align: center;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: 0;
  right: 0;
  margin: auto;
}

.answer {
  font-size: 20vw;
  color: #fff;
}

.button {
  border: 0;
  background: lighten(#222, 10%);
  color: #fff;
  font-weight: bold;
  cursor: pointer;
  width: 50%;
  padding: 20px 0;
  left: 0;
  right: 0;
  margin: auto;
  position: absolute;
  text-transform: uppercase;
  top: 15px;
  font-size: 25px;
  z-index: 3;

  &:nth-of-type(2) {
    top: auto;
    bottom: 15px;
  }
}

.reset {
  position: absolute;
  z-index: 1;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
  outline: 0;
  cursor: pointer;
}